import { config } from "@/utils/cc-components-config/switch";
let props = {
  value: {
    type: [String, Number, Boolean],
    default() {
      return false;
    }
  },
  activeValue: {
    type: [String, Number, Boolean],
    default: config.activeValue
  },
  inactiveValue: {
    type: [String, Number, Boolean],
    default: config.inactiveValue
  },
  activeColor: {
    type: String,
    default:config.activeColor
  },
  inActiveColor: {
    type: String,
    default: config.inActiveColor
  },
  // <code>openMessageBox='true'</code>
  activeMessage: {
    type: String,
    default: config.activeMessage
  },
  // <code>openMessageBox='true'</code>
  inActiveMessage: {
    type: String,
    default: config.inActiveMessage
  },
  // 
  openMessageBox: {
    type: Boolean,
    default: config.openMessageBox
  },
  // 
  confirmFun: Function,
  // 
  cancelFun: Function,
  // <code>openMessageBox='true'</code>
  url: {
    type: String
  },
  // 
  params: {
    type: Object,
    default: () => {
      return config.params;
    }
  },
  // 
  method: {
    type: String,
    default: config.method
  },
  // key
  field: {
    type: String,
    default: config.field
  },
  // loading<code>openMessageBox='true'</code>
  isLoading: {
    type: Boolean,
    default: true
  },
  // 
  disabled: Boolean
};

export default props;
