<template>
  <div v-if="inline" :id="selectorId"></div>
  <div :id="selectorId" v-else></div>
</template>

<script>
import './tinymce.min.js';
import './icons/default/icons.min.js';
import './themes/silver/theme.min.js';
import './langs/zh_CN.js';
import { fontsize_formats, font_formats } from './font_formats';
import plugins from './plugins';
import toolbar from './toolbar';
export default {
  props: {
    value: {
      type: String,
      default: ''
    },
    plugins: {
      type: [String, Array, Boolean],
      default: () => plugins
    },
    /*
    

     | 

    false
  */
    toolbar: {
      type: [String, Array, Boolean],
      default: () => toolbar
    },
    // 
    branding: {
      type: Boolean,
      default: false
    },
    /*
    

    TinyMCEtiny

    language:'zh_CN'
  */
    language: {
      type: String,
      default: 'zh_CN'
    },
    // id
    selectorId: {
      type: String,
      default: `tinymce-id-${new Date().getTime()}`
    },
    /*
    1

    

    false
   */
    menubar: {
      type: [String, Boolean],
      default: 'bar1 file edit insert view format table'
    },
    /*
    

    

     :{  ,  } 

    menubar
   */
    menu: Object,
    /*  */
    fontsize_formats: {
      type: String,
      default: fontsize_formats
    },
    /*  */
    font_formats: {
      type: String,
      default: font_formats
    },
    /*
    

    
  */
    link_list: Object,
    /*
    titlevalue
  */
    image_list: {
      type: Array,
      default: () => {
        return [
          { title: '1', value: 'https://www.tiny.cloud/images/glyph-tinymce@2x.png' },
          { title: '2', value: 'https://www.baidu.com/img/bd_logo1.png' }
        ];
      }
    },
    /* class */
    image_class_list: {
      type: Array,
      default: () => {
        return [
          { title: 'None', value: '' },
          { title: 'Some class', value: 'class-name' }
        ];
      }
    },
    /*
    
    resize: true

    true, false, 'both'

    false
  */
    resize: {
      type: [Boolean, String],
      default: true
    },
    /*
    

    domTiny
  */
    statusbar: {
      type: Boolean,
      default: true
    },
    // 
    toolbar_sticky: {
      type: Boolean,
      default: true
    },
    // true
    importcss_append: {
      type: Boolean,
      default: true
    },
    // URL
    autosave_ask_before_unload: Boolean,
    // 
    height: {
      type: Number,
      default: 650
    },
    // 
    min_height: {
      type: Number,
      default: 400
    },
    // 
    autosave_interval: {
      type: String,
      default: '30s'
    },
    // 
    autosave_retention: {
      type: String,
      default: '30m'
    },
    object_resizing: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {};
  },
  components: {},
  methods: {
    init() {
      // tinymcetinymce
      if (this.$tinymce.editors.length > 0) {
        this.$tinymce.editors.forEach((item, i) => {
          if (item.id == this.selectorId) {
            this.$tinymce.editors[i].remove();
          }
        });
      }
      this.$tinymce.init({
        selector: `#${this.selectorId}`,
        doctype: '<!DOCTYPE html>',
        entities: '160,nbsp,162,cent,8364,euro,163,pound',
        toolbar_mode: 'sliding',
        language: this.language,
        content_css: [
          'http://tinymce.ax-z.cn/static/tiny/skins/ui/oxide/content.min.css',
          '/tinymce-content.css'
        ],
        inline: this.inline,
        branding: this.branding,
        // 
        menubar: this.menubar,
        menu: this.menu,
        // 
        toolbar: this.toolbar,
        //  preview:
        plugins: this.plugins,
        //  tinymce 
        setup: (editor) => {
          editor.on('init', (e) => {
            editor.setContent(this.value);
          });
        },
        //  input  change , value
        init_instance_callback: (editor) => {
          editor.on('input', (e) => {
            this.$emit('input', e.target.innerHTML);
          });
          editor.on('change', (e) => {
            this.$emit('input', e.level.content);
          });
          editor.on('fous', (e) => {
            console.log(1111);
            //  this.$emit('blur', e.level.content);
          });
        },
        // 
        autosave_interval: this.autosave_interval,
        // 
        autosave_retention: this.autosave_retention,
        // URL
        autosave_ask_before_unload: this.autosave_ask_before_unload,
        // 
        resize: this.resize,
        // 
        statusbar: this.statusbar,
        // 
        height: this.height,
        // 
        min_height: this.min_height,
        // 
        fontsize_formats: this.fontsize_formats,
        // 
        font_formats: this.font_formats,
        link_list: this.link_list,
        image_list: this.image_list,
        image_class_list: this.image_class_list,
        importcss_append: this.importcss_append,
        toolbar_sticky: this.toolbar_sticky,
        object_resizing: 'table',
        quickbars_insert_toolbar: this.quickbars_insert_toolbar,
        quickbars_selection_toolbar: this.quickbars_selection_toolbar,
        ...this.$attrs
      });
    }
  },

  mounted() {
    this.init();
  }
};
</script>

<style scoped lang="scss">
@import url('./skins/ui/oxide/skin.min.css');
</style>
