import { config } from '@/utils/cc-components-config/select-virtual-rolling'
let props = {
  options: Array, // optionoptions>url
  url: {
    // URL
    type: String,
    default: config.url
  },
  params: Object, // 
  dataHandler: Function, //
  listField: {
    type: String,
    default: config.listField
  },
  size: { type: String, default: config.size },
  clearable: {
    type: Boolean,
    default: config.clearable
  },
  styles: {
    type: [String, Object, Array],
    default: config.styles
  },
  placeholder: {
    // 
    type: String,
    default: config.placeholder
  },
  optionKeys: {
    type: Object,
    default: () => {
      return config.optionKeys;
    }
  }, // vaulelabel
  disableValues: {
    type: Array,
    default: () => {
      return config.disableValues;
    }
  },
  // 
  disableKey: String,
  showValues: {
    type: Array,
    default: () => {
      return config.showValues;
    }
  },
  // 
  showKey: String,
  isLoading: {
    type: Boolean,
    default: config.isLoading
  },
  dataFilterHandler: Function,
  showOptionSlot: Boolean
}

export default props